import React from "react"
import { Link } from "gatsby"

interface TimelineItemProps {
  slug: string
  title: string
  date: string
  description?: string
  excerpt?: string
  tags?: string[]
}

function TimelineItem({
  slug,
  title,
  date,
  description,
  excerpt,
  tags,
}: TimelineItemProps): React.ReactElement {
  // Format date from "MMMM DD, YYYY" to "2024.01.15"
  const formatDate = (dateStr: string): string => {
    try {
      const date = new Date(dateStr)
      const year = date.getFullYear()
      const month = String(date.getMonth() + 1).padStart(2, "0")
      const day = String(date.getDate()).padStart(2, "0")
      return `${year}.${month}.${day}`
    } catch {
      return dateStr
    }
  }

  const displayDescription = description || excerpt || ""

  return (
    <article className="group relative flex gap-6 sm:gap-8 py-6">
      {/* Timeline line and dot */}
      <div className="flex flex-col items-center">
        {/* Timeline dot */}
        <div
          className="relative z-10 w-3 h-3 rounded-full bg-border transition-all duration-300 group-hover:bg-primary group-hover:shadow-[0_0_8px_var(--primary)]"
          aria-hidden="true"
        />
        {/* Vertical line */}
        <div
          className="w-px flex-1 bg-border mt-2"
          aria-hidden="true"
        />
      </div>

      {/* Content */}
      <div className="flex-1 min-w-0 pb-6">
        {/* Date */}
        <time className="text-sm text-foreground-muted font-medium tabular-nums">
          {formatDate(date)}
        </time>

        {/* Title */}
        <h3 className="mt-2 text-xl sm:text-2xl font-semibold">
          <Link
            to={slug}
            className="text-foreground hover:text-primary transition-colors duration-200"
          >
            {title}
          </Link>
        </h3>

        {/* Description */}
        {displayDescription && (
          <p
            className="mt-2 text-sm sm:text-base text-foreground-muted line-clamp-2"
            dangerouslySetInnerHTML={{ __html: displayDescription }}
          />
        )}

        {/* Tags */}
        {tags && tags.length > 0 && (
          <div className="mt-3 flex flex-wrap gap-2">
            {tags.map((tag) => (
              <Link
                key={tag}
                to={`/tags/${tag.toLowerCase().replace(/\s+/g, "-")}/`}
                className="inline-flex items-center px-2.5 py-0.5 text-xs font-medium text-primary bg-primary-subtle rounded-full hover:bg-primary hover:text-primary-foreground transition-colors duration-200"
              >
                {tag}
              </Link>
            ))}
          </div>
        )}
      </div>
    </article>
  )
}

export default TimelineItem
