import React from "react"
import { Link, graphql, PageProps } from "gatsby"
import { Hash, ArrowLeft, Tag } from "lucide-react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import TimelineItem from "../components/blog/TimelineItem"

interface TagPageContext {
  tag: string
}

interface PostNode {
  node: {
    fields: {
      slug: string
    }
    frontmatter: {
      title: string
      date: string
      description?: string
      tags?: string[]
    }
    excerpt: string
  }
}

interface TagsTemplateData {
  site: {
    siteMetadata: {
      title: string
    }
  }
  allMarkdownRemark: {
    totalCount: number
    edges: PostNode[]
  }
}

/**
 * Tag Detail Page Component
 * Displays all articles for a specific tag with TimelineItem layout
 */
const TagsTemplate: React.FC<
  PageProps<TagsTemplateData, TagPageContext>
> = ({ pageContext, data, location }) => {
  const { tag } = pageContext
  const { edges, totalCount } = data.allMarkdownRemark
  const siteTitle = data.site.siteMetadata.title

  return (
    <Layout location={location} title={siteTitle}>
      <SEO
        title={`标签: ${tag}`}
        keywords={[tag, "标签", "tags", "blog", "文章分类"]}
      />

      <div className="max-w-4xl mx-auto">
        {/* Back Link */}
        <div className="mb-6 fade-in">
          <Link
            to="/tags"
            className="inline-flex items-center gap-2 text-sm text-foreground-muted hover:text-primary transition-colors duration-200"
          >
            <ArrowLeft className="w-4 h-4" />
            <span>返回所有标签</span>
          </Link>
        </div>

        {/* Page Header */}
        <header className="mb-10 fade-in">
          <div className="flex items-center gap-4 mb-4">
            {/* Tag Icon */}
            <div className="flex items-center justify-center w-14 h-14 rounded-2xl bg-primary-subtle">
              <Tag className="w-7 h-7 text-primary" />
            </div>

            {/* Title and Count */}
            <div>
              <h1 className="text-3xl sm:text-4xl font-bold text-foreground">
                #{tag}
              </h1>
              <p className="mt-1 text-foreground-muted">
                共{" "}
                <span className="text-primary font-semibold">{totalCount}</span>{" "}
                篇文章
              </p>
            </div>
          </div>
        </header>

        {/* Articles List */}
        {edges.length === 0 ? (
          /* Empty State */
          <div className="text-center py-20 fade-in">
            <div className="inline-flex items-center justify-center w-20 h-20 rounded-full bg-background-secondary mb-4">
              <Hash className="w-10 h-10 text-foreground-muted" />
            </div>
            <h3 className="text-xl font-semibold text-foreground mb-2">
              暂无文章
            </h3>
            <p className="text-foreground-muted mb-6">
              该标签下还没有文章，去看看其他标签吧
            </p>
            <Link
              to="/tags"
              className="inline-flex items-center gap-2 px-4 py-2 rounded-lg bg-primary text-primary-foreground font-medium hover:bg-primary/90 transition-colors duration-200"
            >
              <ArrowLeft className="w-4 h-4" />
              <span>浏览所有标签</span>
            </Link>
          </div>
        ) : (
          /* Timeline Articles List */
          <div className="fade-in-up">
            {edges.map(({ node }, index) => (
              <div
                key={node.fields.slug}
                className="fade-in-up"
                style={{ animationDelay: `${index * 100}ms` }}
              >
                <TimelineItem
                  slug={node.fields.slug}
                  title={node.frontmatter.title}
                  date={node.frontmatter.date}
                  description={node.frontmatter.description}
                  excerpt={node.excerpt}
                  tags={node.frontmatter.tags}
                />
              </div>
            ))}
          </div>
        )}

        {/* Bottom Back Link */}
        {edges.length > 0 && (
          <div className="mt-10 pt-8 border-t border-border fade-in">
            <Link
              to="/tags"
              className="inline-flex items-center gap-2 text-sm text-foreground-muted hover:text-primary transition-colors duration-200"
            >
              <ArrowLeft className="w-4 h-4" />
              <span>返回所有标签</span>
            </Link>
          </div>
        )}
      </div>
    </Layout>
  )
}

export default TagsTemplate

export const pageQuery = graphql`
  query ($tag: String) {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(
      limit: 2000
      sort: { frontmatter: { date: DESC } }
      filter: { frontmatter: { tags: { in: [$tag] } } }
    ) {
      totalCount
      edges {
        node {
          fields {
            slug
          }
          frontmatter {
            title
            date(formatString: "MMMM DD, YYYY")
            description
            tags
          }
          excerpt(truncate: true, pruneLength: 160)
        }
      }
    }
  }
`
